<template>
  <div class="cms-list-layout" style="padding-top:6px">
    <el-tabs v-model="activeName" v-if="status" ref='settingTab'>
      <el-tab-pane label="基本设置" name="first" lazy>
        <common></common>
      </el-tab-pane>
      <el-tab-pane label="版本管理" name="second" lazy>
        <version></version>
      </el-tab-pane>
      <el-tab-pane label="成员管理" name="third" lazy>
        <member></member>
      </el-tab-pane>
      <el-tab-pane label="服务器域名" name="fourth" lazy>
        <domain></domain>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import common from './common'
import version from './version'
import member from './member'
import domain from './domain'
export default {
  name: 'settingTab',
  components: {
    common,
    version,
    member,
    domain
  },
  data() {
    return {
      status: true,
      activeName: 'first'
    }
  },
  methods: {
    toggleTabs() {
      if (this.$route.query.activeStatus === '3') {
        this.activeName = 'third'
      }
    }
  },
  watch: {
    '$router': {
      handler: 'toggleTabs',
      immediate: true
    }
  },
  activated() {
    if (this.$route.query.type === 'list') {
      this.status = false
      setTimeout(() => {
        this.status = true
      }, 1)
    }
  },
  created() {
    if (this.$route.query.activeStatus === '3') {
      this.activeName = 'third'
    }
  }
}
</script>

<style scoped>
.el-tabs__header {
    margin: 0 0 10px !important;
}
</style>



