<template>
    <div v-loading='loading' class="all">
    <cms-breadcrumb to="/wechatPublic/list?activeStatus=2">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-list-layout">
      <cms-prompt collapse="true" >
        <li>管理员可添加小程序项目成员，并配置成员的权限，
          <a href="http://kf.qq.com/faq/170302zeQryI170302beuEVn.html" style="color:#459AE9;" target="blank">点击查看详情</a> 
        </li>
        <li>在微信小程序管理后台添加体验者在此无法显示</li>
      </cms-prompt>
      <div style="margin:5px 0">
        <el-button @click="addMemberBtn" size="mini" type="primary">添加体验者</el-button>
      </div>
       <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" ref='dataTable'>
      <el-table-column prop="wechatId" label="成员" min-width='220'></el-table-column>
      <el-table-column prop="createTime" label="加入时间" min-width="260">
        <template slot-scope="scope">
          <span>{{$moment(scope.row.createTime)}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button  icon='error' size='small' type='info' @click="memberDelete(scope.row.id,scope.row.wechatId)">解绑</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <!-- 弹出的对话框 -->
    <el-dialog
       title="添加体验者"
       :visible.sync="dialogVisible"
       width="30%"
      :before-close="handleClose">
      <div style="padding:30px 10px;">
        <span>微信号:</span>
        <el-input v-model="wechatId" style="width:200px;margin-left:10px;" maxlength="20"></el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="dialogVisible = false;wechatId = ''">取 消</el-button>
        <el-button size="mini" type="primary" @click.stop="addMember">确 定</el-button>
       </span>
    </el-dialog>
    </div>
  </div>
</template>

<script>
import { miniProgramMemberAdd, miniProgramMemberFindAll, miniProgramMemberDelete } from '@/api/wechat/miniProgram.js'
export default {
  data() {
    return {
      dialogVisible: false, // 弹出框的显示隐藏
      wechatId: '', // 微信号
      loading: false,
      tableData: []
    }
  },
  methods: {
    addMember() { // 添加体验者
      if (this.wechatId === '' || /^\s/.test(this.wechatId)) {
        this.$errorMessage('请输入微信号')
      } else if (/[\u4e00-\u9fa5]/g.test(this.wechatId)) {
        this.$errorMessage('微信号格式不正确')
      } else {
        miniProgramMemberAdd({ wechatId: this.wechatId }).then(res => {
          if (res.code === 200) {
            this.$successMessage('添加成功!')
            this.dialogVisible = false
            this.getMemberList()
            this.wechatId = ''
          } else if (res.code === 12518) {
            this.$errorMessage('该微信号已经绑定了小程序,请更换微信号')
          }
        })
      }
    },
    memberDelete(id, wechatId) { // 解绑
      miniProgramMemberDelete
      this.$confirm('确定解绑此账号?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        miniProgramMemberDelete({ id: id, wechatId: wechatId }).then(res => {
          if (res.code === 200) {
            this.$successMessage('解绑成功!')
            this.getMemberList()
          }
        })
      })
    },
    addMemberBtn() { // 添加体验者按钮
      this.dialogVisible = true
    },
    handleClose(done) { // 关闭清除微信号
      done()
      this.wechatId = ''
    },
    getMemberList() {
      miniProgramMemberFindAll().then(res => {
        if (res.code === 200) {
          this.tableData = res.data
        } else {
          return false
        }
      })
    }
  },
  created() {
    this.getMemberList()
  }
}
</script>

<style>

</style>



