<template>
  <div v-loading="loading">
    <div class="version_wrap">
      <div class="version__title">
        <span class="version__em">开发版本</span>
        <el-tooltip class="item" effect="dark" content="推荐使用模板库中的最新版本" placement="right">
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
      <div class="version__item" v-if="newVersion">
        <div class="version__main">
          <div class="version__order">
            <p>版本号</p>
            <p
              style="font-weight:bold;font-size:16px;margin-top:5px;"
              v-if="newVersion.codeVersion"
            >{{newVersion.codeVersion}}</p>
          </div>
          <div class="version__info">
            <p class="version__decs">提交时间：{{$moment(newVersion.createTime)}}</p>
            <p
              class="version__decs"
              v-if="newVersion.hasOwnProperty('failReason')"
            >失败原因：{{newVersion.failReason}}</p>
          </div>
          <div class="version__btn">
            <el-button type="primary" size="mini" @click="addSubmit" v-show="btnIsShow[1]">上传代码</el-button>
            <el-button
              type="success"
              size="mini"
              @click="dialogVisibleOuter = true"
              v-show="btnIsShow[3]"
            >提交审核</el-button>
          </div>
        </div>
      </div>
      <div class="version__empty" style="width:140px;height:140px;margin:0 auto" v-if="!newVersion">
        <img src="@/assets/images/version-en.png" alt="empty">
        <p style="font-size:12px;color:rgb(96, 98, 102)">暂无开发版本信息</p>
      </div>
    </div>
    <div class="version_wrap">
      <span class="version__em">审核版本</span>
      <span style="display:inline-block" class="version__title" v-if="auditVersion">
        <span class="audit__fail" v-show="auditVersion.status==3&&auditVersion">审核失败</span>
        <span class="audit__success" v-show="auditVersion.status==2&&auditVersion">审核成功</span>
        <span class="audit__ing" v-show="auditVersion.status==1&&auditVersion">审核中...</span>
      </span>
      <div class="version__item" v-if="auditVersion">
        <div class="version__main">
          <div class="version__order">
            <p>
              版本号
              <span @click="popCode" v-show="btnIsShow[7]">
                <i class="el-icon-menu" style="margin-left:5px"></i>
              </span>
            </p>
            <p
              style="font-weight:bold;font-size:16px;cursor:pointer;margin-top:5px;"
              v-if="auditVersion"
            >{{auditVersion.codeVersion}}</p>
          </div>
          <div class="version__info">
            <p class="version__decs">提审时间：{{$moment(auditVersion.createTime)}}</p>
            <p
              class="version__decs"
              v-if="auditVersion.status!=1"
            >审核时间：{{$moment(auditVersion.auditTime)}}</p>
            <p
              class="version__decs"
            >说&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 明：微信审核中，请耐心等待。一般审核周期为1-7个工作日，审核成功后即可操作小程序</p>
            <div v-if="auditVersion.status==3">
              <p style="display:inline-block;vertical-align: top;">失败原因：</p>
              <p style="display:inline-block" class="version__decs" v-html="auditVersion.failReason"></p>
            </div>
          </div>
          <div class="version__btn">
            <el-button
              type="success"
              size="mini"
              @click="addRelease(auditVersion.id)"
              v-show="btnIsShow[2]"
            >发布</el-button>
            <el-button
              type="danger"
              size="mini"
              @click="cancelAudit(auditVersion.appId,auditVersion.id)"
              v-show="btnIsShow[4]"
            >撤回审核</el-button>
            <el-button
              type="primary"
              size="mini"
              @click="dialogVisibleOuter = true"
              v-show="btnIsShow[5]"
            >重新审核</el-button>
          </div>
        </div>
      </div>
      <div
        class="version__empty"
        style="width:140px;height:140px;margin:0 auto"
        v-if="!auditVersion"
      >
        <img src="@/assets/images/version-en.png" alt="empty">
        <p style="font-size:12px;color:rgb(96, 98, 102)">暂无审核版本信息</p>
      </div>
    </div>
    <div class="version_wrap release__wrap">
      <span class="version__em">线上版本</span>
      <div class="version__title" v-if="releaseVersion">
        <span class="audit__fail" v-if="releaseVersion.action=='close'&&releaseVersion">隐藏</span>
        <span class="audit__success" v-if="releaseVersion.action=='open'&&releaseVersion">可见</span>
      </div>
      <div class="version__item" v-if="releaseVersion">
        <div class="version__main">
          <div class="version__order">
            <p>版本号</p>
            <p style="font-weight:bold;font-size:16px;">{{releaseVersion.codeVersion}}</p>
          </div>
          <div class="version__info">
            <p class="version__decs">上线时间：{{$moment(releaseVersion.createTime)}}</p>
            <p
              class="version__decs"
            >说&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;明：隐藏的线上版本，微信用户无法搜索或访问，可见的线上版本可正常使用</p>
          </div>
          <div class="version__btn">
            <el-button
              type="danger"
              size="mini"
              @click="updateReleaseStatusClose(releaseVersion.appId,releaseVersion.id)"
              v-if="releaseVersion.action=='open'"
            >设为隐藏</el-button>
            <el-button
              type="primary"
              size="mini"
              @click="updateReleaseStatusOpen(releaseVersion.appId,releaseVersion.id)"
              v-if="releaseVersion.action=='close'"
            >设为可见</el-button>
          </div>
        </div>
        <p class="upload" style="color:#FF9911" v-show="btnIsShow[6]">
          <i class="el-icon-warning"></i>
          <span>版本更新提示:当前最新版本为{{advancedVersion}},推荐提交审核上线最新版本</span>
        </p>
      </div>
      <div
        class="version__empty"
        style="width:140px;height:140px;margin:0 auto"
        v-if="!releaseVersion"
      >
        <img src="@/assets/images/version-en.png" alt="empty">
        <p style="font-size:12px;color:rgb(96, 98, 102)">暂无版本已发布上线</p>
      </div>
    </div>
    <!-- 提交审计弹出外层对话框 -->
    <el-dialog
      title="提交审核的相关须知"
      :visible.sync="dialogVisibleOuter"
      width="55%"
      center
      :before-close="handleClose"
    >
      <div class="outer__wrap" style="width:530px;margin:0 auto">
        <div style="height:135px;text-align:center">
          <img src="@/assets/images/danger.png" alt="warning">
          <p>确认提交审核？</p>
        </div>
        <p>提交给微信团队审核前，请确保：</p>
        <ul>
          <li class="audit__li">
            提交的小程序功能完整，可正常打开和运行，而不是测试版或 Demo
            小程序的调试和预览可在开发者工具进行。 多次提交测试内容或 Demo，将受到相应处罚。
          </li>
          <li class="audit__li">请慎重进行提交审核操作，在审核反馈前（1-7日）不能重复提交审核</li>
          <li class="audit__li">
            已仔细阅读
            <a
              href="https://developers.weixin.qq.com/miniprogram/product/index.html"
              target="blank"
              style="color:#47A8EE"
            >《微信小程序平台运营规范》</a>
            和
            <a
              href="https://developers.weixin.qq.com/miniprogram/product/reject.html"
              target="blank"
              style="color:#47A8EE"
            >《微信小程序平台审核常见被拒绝情形》</a>
          </li>
        </ul>
        <div style="text-align:center;margin:10px 0;">
          <el-checkbox v-model="auditChecked">已阅读并了解平台审核规则</el-checkbox>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleOuter = false;auditChecked=false">取 消</el-button>
        <el-button type="primary" @click="handleDialog" :disabled="!auditChecked">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 提交审计弹出内层对话框 -->
    <el-dialog
      title="提交审核"
      width="50%"
      :visible.sync="dialogVisibleInner"
      :before-close="handleClose"
    >
      <div class="inner__wrap" style="width:530px;margin:0 auto">
        <p style="font-size:16px;margin:10px 0 5px 0;">
          配置功能页面
          <span style="font-size:12px;">(至少填写一组，填写正确的信息有利于用户快速搜索出你的小程序)</span>
        </p>
        <div
          class="inner__item"
          style="font-size:13px;padding:0 10px;"
          v-for="(auditInfo,index) in auditInfoList"
          :key="index"
        >
          <p style="font-size:14px; color:#333;">功能页面{{index+1}}</p>
          <div style="padding:0 20px">
            <span class="inner__tips">功能页面</span>
            <el-select
              v-model="auditInfo.address"
              clearable
              placeholder="请选择功能页面"
              style="width:230px;"
            >
              <el-option v-for="item in auditPageConfig" :key="item" :label="item" :value="item"></el-option>
            </el-select>
            <br>
            <span class="inner__tips">标&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;题</span>
            <el-input style="width:230px;" v-model="auditInfo.title" maxlength="20"></el-input>
            <p class="inner__warn gray">小程序内的页面标题显示</p>
            <span class="inner__tips">所在服务类目</span>
            <el-select
              v-model="auditInfo.categoryDto"
              clearable
              value-key="id"
              style="width:230px;"
            >
              <el-option
                v-for="item in auditCategory"
                :key="item.id"
                :label="item.categoryName"
                :value="item"
              ></el-option>
            </el-select>
            <p class="inner__warn gray">功能页面和服务类目必须一一对应，且功能页面提供的内容必须符合该类目范围</p>
            <div>
              <span class="inner__tips">标&nbsp;&nbsp;&nbsp;&nbsp;签</span>
              <el-input
                placeholder="请输入内容"
                style="width:230px;"
                v-model="auditInfo.tag"
                maxlength="30"
              ></el-input>
            </div>
            <p class="inner__warn">搜索标签，标签填写与页面功能相关，更容易被搜索</p>
            <!-- 删除一个功能页面 -->
            <div style="border-top:1px dashed #e0e0e0;height:36px;">
              <span
                @click.stop="delAuditBtn(index)"
                style="margin-left:398px;color:#F56C6C;cursor:pointer;"
                v-if="index!=0"
                v-show="index>=1"
              >删除</span>
            </div>
          </div>
        </div>
        <div
          style="cursor:pointer;position:relative;top:-10px;margin-left:30px;"
          v-if="auditInfoList.length<5"
          :class="{'add__one':auditInfoList.length==1}"
        >
          <span @click.stop="addAuditBtn">
            <i class="el-icon-circle-plus" style="color:#67C23A"></i> 添加功能页面
          </span>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addAuditCancel" size="mini">取 消</el-button>
        <!-- 提交审计(新增一个审核版本)按钮 -->
        <el-button type="primary" @click="addAudit" size="mini">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 弹出的二维码 -->
    <el-dialog title="体验二维码" :visible.sync="dialogVisibleCode" width="30%">
      <div style="width:220px;height:240px;margin:0 auto;background-color:#F4F5F6;padding:8px;">
        <div style="width:200px;height:200px;margin:0 auto;">
          <img :src="popCodeUrl" alt="code" width="200px" height="200px">
        </div>
        <p style="text-align:center;margin-top:5px">
          <i class="el-icon-success" style="color:#009900;margin-right:5px;"></i>体验版已生效
        </p>
      </div>
      <p style="text-align:center;margin:10px 0 20px 0">
        未添加体验者,
        <a href="javascript:;" style="color:#459AE9;" @click="toMember">点击添加</a>
      </p>
    </el-dialog>
  </div>
</template>
<script>
import {
  newVersionGet,
  auditVersionGet,
  releaseVersionGet,
  versionPageConfig,
  versionGetCategory,
  versionDeleteAudit,
  versionAddSubmit,
  versionIsShow,
  versionAddAudit,
  getQrcode,
  versionAddRelease,
  updateReleaseStatus
} from '@/api/wechat/miniProgram.js'
import list from '@/mixins/list'
import axios from 'axios'
export default {
  mixins: [list],
  data() {
    return {
      newVersion: '',
      auditVersion: '',
      releaseVersion: '',
      auditPageConfig: null, // 审计页面配置
      auditCategory: [], // 审计可选类目
      dialogVisibleOuter: false, // 审计按钮外层对话框的显示隐藏
      dialogVisibleInner: false, // 审计按钮内层对话框的显示隐藏
      dialogVisibleCode: false, // 弹出的二维码的显示隐藏
      popCodeUrl: '', // 弹出二维码的路径
      auditChecked: false, // 审计按钮外层单选框
      auditInfoList: [{ address: '', tag: '', title: '', categoryDto: '' }], // 新增审核版本的数据
      btnIsShow: '', // 所有按钮的状态
      baseUrl: process.env.baseUrl,
      loading: false,
      advancedVersion: '' // 最新版本的版本号
    }
  },
  methods: {
    getAllVersions() {
      // 所有版本信息及按钮状态
      axios
        .all([
          newVersionGet(),
          auditVersionGet(),
          releaseVersionGet(),
          versionIsShow()
        ])
        .then(
          axios.spread(
            (newVersions, auditVersions, releaseVersions, versionShows) => {
              this.newVersion = newVersions.data
              if (newVersions.data.codeVersion) {
                this.advancedVersion = newVersions.data.codeVersion // 获取最新版本号
              }
              this.auditVersion = auditVersions.data
              this.releaseVersion = releaseVersions.data
              this.btnIsShow = versionShows.data
            }
          )
        )
    },
    getPageConfig() {
      // 获取页面配置
      versionPageConfig().then(res => {
        if (res.code === 200) {
          this.auditPageConfig = res.data
        }
      })
    },
    getOptionalCategory() {
      // 获取可选类目
      versionGetCategory().then(res => {
        if (res.code === 200) {
          this.auditCategory = res.data
        }
      })
    },
    addSubmit() {
      // 上传代码
      this.$confirm('确定要上传代码?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        versionAddSubmit().then(res => {
          if (res.code === 200) {
            if (res.data) {
              this.$successMessage('上传成功')
              this.getAllVersions()
            } else {
              this.$errorMessage('上传失败')
            }
          } else {
            this.$errorMessage(res.message)
          }
        })
      })
    },
    handleDialog() {
      // 控制内外层对话框的显示隐藏及获取审计下拉列表内容(外层对话框的确定按钮事件)
      this.dialogVisibleOuter = false
      this.dialogVisibleInner = true
      this.getPageConfig()
      this.getOptionalCategory()
    },
    popCode() {
      // 弹出二维码
      getQrcode().then(res => {
        if (res.code === 200) {
          this.popCodeUrl = 'data:image/jpeg;base64,' + res.data
          this.dialogVisibleCode = true
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    toMember() {
      // 二维码中跳转到添加体验者
      this.$router.push('/wechatPublic/miniSetting?activeStatus=3')
      window.location.reload()
      this.dialogVisibleCode = false
    },
    addAuditBtn() {
      this.auditInfoList.push({
        address: '',
        tag: '',
        title: '',
        categoryDto: ''
      })
    },
    delAuditBtn(index) {
      // 删除一个功能页面
      this.auditInfoList.splice(index, 1)
    },
    addAuditCancel() {
      // 提交审计内层页面的取消按钮事件
      this.dialogVisibleInner = false // 关闭弹出框
      this.auditInfoList = [
        { address: '', tag: '', title: '', categoryDto: '' }
      ] // 清空页面所填的信息
      this.auditChecked = false // 单选框重置
    },
    addAudit() {
      // 提交审计(新增一个审核版本)
      let flag = true
      this.auditInfoList.forEach(item => {
        // 判断是否有未填项
        if (
          item.address === '' ||
          item.tag === '' ||
          item.title === '' ||
          item.categoryDto === ''
        ) {
          flag = false
          this.$errorMessage('请全部填写后完成后再提交')
          delete item.categoryDto.id // 删除可选类目的id
        }
        if (flag) {
          versionAddAudit(this.auditInfoList).then(res => {
            if (res.code === 200) {
              this.$successMessage('提交审计成功')
              this.dialogVisibleInner = false
              this.auditInfoList = [
                { address: '', tag: '', title: '', categoryDto: '' }
              ]
              this.getAllVersions()
              this.auditChecked = false // 重置审计按钮外层单选框
            }
          })
        }
      })
    },
    addRelease(id) {
      // 发布按钮(新增一个发布版本)
      this.$confirm('确定发布?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        versionAddRelease({ id: id }).then(res => {
          // 传入审计版本的ID值
          if (res.code === 200) {
            this.$successMessage('发布成功!')
            this.getAllVersions()
          } else {
            this.$errorMessage(res.message)
          }
        })
      })
    },
    cancelAudit(appId, id) {
      // 撤销审核
      this.$confirm('确定要撤销审核?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        versionDeleteAudit({ appId: appId, id: id }).then(res => {
          // 传入小程序appId和版本id值
          if (res.code === 200) {
            if (res.data === true) {
              this.$successMessage('撤回审核成功!')
              this.getAllVersions()
            } else {
              this.$errorMessage(res.data)
            }
          } else {
            this.$errorMessage('撤回审核失败')
          }
        })
      })
    },
    updateReleaseStatusClose(appId, id) {
      // 修改发布版本状态(设为隐藏)
      const params = {
        id: id,
        appId: appId,
        action: 'close'
      }
      this.loading = true
      updateReleaseStatus(params).then(res => {
        if (res.code === 200) {
          this.$successMessage('设置成功!')
          this.getAllVersions()
          this.loading = false
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    },
    updateReleaseStatusOpen(appId, id) {
      const params = {
        id: id,
        appId: appId,
        action: 'open'
      }
      this.loading = true
      updateReleaseStatus(params).then(res => {
        if (res.code === 200) {
          this.$successMessage('设置成功!')
          this.getAllVersions()
          this.loading = false
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    },
    handleClose(done) {
      // 内外两层弹窗的关闭按钮
      this.auditInfoList = [
        { address: '', tag: '', title: '', categoryDto: '' }
      ]
      this.auditChecked = false // 重置审计按钮外层单选框
      done()
    }
  },
  created() {
    this.getAllVersions()
  }
}
</script>
