  line-height: 25px;
  word-break: break-all;
}
/* 线上版本样式控制 */
.release__wrap {
  position: relative;
}
.upload {
  position: absolute;
  bottom: 20px;
}
/* 内层弹出框的样式控制 */
.inner__item {
  line-height: 44px;
  padding-bottom: 5px;
}
.inner__tips {
  display: inline-block;
  width: 100px;
  text-align: right;
  margin-right: 80px;
}
.inner__warn {
