<template>
  <div>
    <table class="domian-table" style>
      <tr class="domain__nav">
        <td>
          <div class="domain__title">
            <div class="fl">服务器域名
              <el-tooltip class="item" effect="dark" content="一个月内只能申请修改五次" placement="top-start">
                <i class="el-icon-question"></i>
              </el-tooltip>
            </div>
            <div class="fr">
              <el-button @click="modifyDomain" size="mini" type="primary">重置合法域名</el-button>
            </div>
          </div>
        </td>
      </tr>
      <tr>
        <td>
          <div class="domain__wrap">
            <div class="domain__left">request合法域名</div>
            <div style="height:100px;display:table;" class="domain__right">
              <span
                style="display:table-cell;vertical-align:middle;"
                v-for="(item,index) in domainList.requestdomain"
                :key="index"
              >{{item}}</span>
            </div>
          </div>
        </td>
      </tr>
      <tr>
        <td>
          <div class="domain__wrap">
            <div class="domain__left">scoket合法域名</div>
            <div style="height:100px;display:table;" class="domain__right">
              <span
                style="display:table-cell;vertical-align:middle;"
                v-for="(item,index) in domainList.wsrequestdomain"
                :key="index"
              >{{item}}</span>
            </div>
          </div>
        </td>
      </tr>
      <tr>
        <td>
          <div class="domain__wrap">
            <div class="domain__left">uploadFile合法域名</div>
            <div style="height:100px;display:table;" class="domain__right">
              <span
                style="display:table-cell;vertical-align:middle;"
                v-for="(item,index) in domainList.uploaddomain"
                :key="index"
              >{{item}}</span>
            </div>
          </div>
        </td>
      </tr>
      <tr>
        <td>
          <div class="domain__wrap">
            <div class="domain__left">downloadFile合法域名</div>
            <div style="height:100px;display:table;" class="domain__right">
              <span
                style="display:table-cell;vertical-align:middle;"
                v-for="(item,index) in domainList.downloaddomain"
                :key="index"
              >{{item}}</span>
            </div>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import {
  UserStatisticsDisplayModifyDomain,
  UserStatisticsModifyDomain
} from '@/api/wechat/miniProgram.js'
export default {
  data() {
    return {
      domainList: '' // 域名列表
    }
  },
  methods: {
    getDomainList() {
      // 获取域名列表
      UserStatisticsDisplayModifyDomain().then(res => {
        if (res.code === 200) {
          this.domainList = res.data
        }
      })
    },
    modifyDomain() {
      // 重置合法域名
      UserStatisticsModifyDomain().then(res => {
        if (res.code === 200) {
          this.$successMessage('重置成功!')
          this.getDomainList()
        } else {
          this.$errorMessage(res.message)
        }
      })
    }
  },
  created() {
    this.getDomainList()
  }
}
</script>

<style scoped>
.domian-table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #e0e0e0;
}
.domain__nav {
  background-color: #fafafa;
  height: 45px;
  line-height: 40px;
  font-size: 14px;
  color: #444;
}
.domain__title {
  padding: 0 10px;
  overflow: hidden;
}
.domain__wrap {
  width: 100%;
  min-height: 100px;
  padding: 0 15px;
  overflow: hidden;
}
.domain__left {
  float: left;
  width: 150px;
  line-height: 100px;
  font-size: 13px;
  color: #444;
}
.domain__right {
  float: left;
  margin-left: 100px;
  color: #666;
}
</style>



